<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model.trim="queryInfo.query" clearable @clear="getOrdersList"> </el-input>
        </el-col>
        <el-col :span="2"> <el-button type="primary" icon="el-icon-search" @click="queryOrdersList"></el-button></el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" style="width: 100%" border stripe>
        <el-table-column type="index" width="50px"> </el-table-column>
        <el-table-column prop="order_number" label="" align="left"> </el-table-column>
        <el-table-column prop="order_price" label="()" width="150px" align="center"> </el-table-column>
        <el-table-column prop="order_pay" label="" width="150px" align="center">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay === '0'"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="100px" align="center"> </el-table-column>
        <el-table-column label="" width="230px" align="center">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="editLocation(scope.row)"></el-button>
            <el-button size="mini" type="success" icon="el-icon-location" @click="showLocation(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 20, 100]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total" background> </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editOrderDialogVisible" width="45%" @close="editOrderCaterDialogClosed">
      <el-form :model="editOrderForm" :rules="editOrderFormRules" ref="editOrderFormRef" label-width="80px">
        <el-form-item label="" prop="location1">
          <!-- Cascader  -->
          <el-cascader class="previewImg" :options="citydata" :props="cascaderProps" clearable v-model="editOrderForm.location1" @change="cascaderChooseChange"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="location2">
          <el-input v-model="editOrderForm.location2"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOrderDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editOrderDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="logisticsDialogVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
// citydata 
// citydata  
import citydata from '@/components/order/citydata.js'
export default {
  name: 'Order',
  data() {
    return {
      // 
      queryInfo: {
        //  
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      // 
      ordersList: [],
      // 
      total: 0,
      // 
      editOrderDialogVisible: false,
      // 
      editOrderForm: {
        location1: [],
        //  
        location2: ''
      },
      // 
      editOrderFormRules: {
        location1: [{ required: true, message: '', trigger: 'blur' }],
        location2: [{ required: true, message: '', trigger: 'blur' }]
      },
      //   citydata 
      citydata,
      // Cascader  
      cascaderProps: {
        expandTrigger: 'hover'
      },
      // 
      logisticsDialogVisible: false,
      // 
      progressInfo: []
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      // console.log(this.queryInfo)
      const { data: res } = await this.$http.get('/orders', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // this.$message.success(res.meta.msg)
      this.total = res.data.total
      this.ordersList = res.data.goods
      // console.log(this.ordersList)
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },
    //    
    queryOrdersList() {
      if (this.queryInfo.query.length === 0) {
        this.$message.info('')
        return
      }
      this.$message.error('')
      // this.getOrdersList()
    },
    //  
    editLocation(ojb) {
      this.editOrderDialogVisible = true
    },
    // 
    editOrderCaterDialogClosed() {
      // 
      this.$refs.editOrderFormRef.resetFields()
      this.editOrderForm.location2 = ''
    },
    // Cascader  
    cascaderChooseChange() {
      // console.log(this.editOrderForm.location1)
      // console.log(this.editOrderForm.location2)
    },
    //  
    async showLocation(ojb) {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // this.$message.success(res.meta.message)
      this.progressInfo = res.data
      this.logisticsDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped></style>
